import React from 'react'
import PropTypes from 'prop-types'
import UnitCard from './UnitCard'

const RosterMonitor = (props) => {
    const {
        rosterUnits, handleAdd, handleEdit,
    } = props

    return (
        <div className="roster__monitor">
            <h3>
                Hire more members:
            </h3>
            <div className="roster__monitor--listing">
                {
                    rosterUnits.map(baseUnit => (
                        <UnitCard
                            key={baseUnit.ID}
                            baseUnit={baseUnit}
                            inSquad={false}
                            clickAdd={handleAdd}
                            clickEdit={handleEdit}
                            clickDelete={{ enabled: false }}
                        />
                    ))
                }
            </div>
        </div>
    )
}

RosterMonitor.propTypes = {
    rosterUnits: PropTypes.array.isRequired,
    handleEdit: PropTypes.object.isRequired,
    handleAdd: PropTypes.object.isRequired,
}

export default RosterMonitor
