import { createSelector } from 'reselect'

const getBattle = state => state.battle

const getBattleData = createSelector(
    getBattle,
    (battle) => {
        if (!battle || !battle.stage) {
            return {
                currentTitle: 'loading',
            }
        }
        const { stage: { location }, currentStep } = battle
        const { title: currentTitle } = location[currentStep]
        return { ...battle, currentTitle }
    }
)

export default getBattleData
